.contacts {
  display: flex;
  flex: 1 1 auto;
}
.wrapper {
  text-align: center;
}

.file {
  position: relative;
  cursor: pointer;
  display: flex;
  align-items: flex-end;
  line-height: 24px;
  color: var(--text);
  width: 100%;
  height: 24px;
  padding-left: 55px;
}
.file:hover {
  background-color: var(--tab-active);
}

.file:after {
  content: "";
  position: absolute;
  top: 2px;
  left: 30px;
  height: 20px;
  width: 20px;
  background: url("../../assets/icons/pdf.svg") center/contain no-repeat;
}

.info {
  padding: 30px;
  color: var(--text);
  width: 100%;
  flex: 1 1 auto;
}
.info h1,
.info h2 {
  font-size: 3rem;
  margin: 3rem 0;
  color: var(--code-color-06);
  text-shadow: var(--shadow);
  text-align: center;
}
.info ul {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 2rem;
}
.info li {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  margin-right: 3rem;
}
.info svg,
.info img {
  max-width: 100px;
  height: 100px;
  width: 100%;
  filter: drop-shadow(0 0 2px rgb(0 0 0 / 0.4));
}
.info a {
  opacity: 0.75;
  transition: transform 400ms cubic-bezier(0, 0, 0.2, 1);
}

.info a:hover {
  opacity: 1;
  filter: brightness(1.1);
  transform: translateY(-10px);
}

.svgWrapper {
  display: none;
}
@media (max-width: 767px) {
  .file {
    line-height: 40px;
    height: 40px;
  }
  .file:after {
    top: 3px;
    left: 20px;
    height: 32px;
    width: 32px;
  }
}

@media (max-width: 600px) {
  .info svg,
  .info img {
    max-width: 50px;
    height: 50px;
  }
  .info li {
    margin-right: 1rem;
  }
  .info h1,
  .info h2 {
    font-size: 2rem;
  }
  .info h1 {
    margin-top: 0;
  }
}
